import Vue from "vue";
import Vuex from "vuex";
import { i18n, selectedLocale, languages } from "@/plugins/i18n";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    locale: selectedLocale,
    locales: languages,
    initData: {
      setting: {
        nameOfSetting: null,
        serverIp: null,
        clientIp: null,
        isStartWithWindows: null,
        isSearchForSubtitle: null,
        moviesPath: null,
        defaultCoverImagePath: null,
        defaultPosterImagePath: null,
        clientPlatform: null
      },
      movieTypes: null,
      movieSizes: null,
      movieCategories: null,
      movieKeywords: null
    },
    refreshMovieResult: {
      deletedMoviesCount: null,
      insertedMoviesCount: null,
      allChangedMoviesCount: null,
      deletedMovieNames: [],
      insertedMovieNames: []
    },
    movieCounts: {
      localMoviesCount: null,
      tmdbMoviesCount: null
    }
  },
  mutations: {
    updateLocale(state, newLocale) {
      state.locale = newLocale;
    },
    updateInitData(state, newInitData) {
      state.initData = newInitData;
    },
    updateRefreshMovieResult(state, newRefreshMovieResult) {
      state.refreshMovieResult = newRefreshMovieResult;
    },
    updatemovieCounts(state, newMovieCounts) {
      state.movieCounts = newMovieCounts;
    }
  },
  actions: {
    changeLocale({ commit }, newLocale) {
      i18n.locale = newLocale;
      commit("updateLocale", newLocale);
    }
  }
});



