<template>
  <div class="language-picker" :id="id || title">
    <el-select v-model="lang" value-key="extendedName">
      <el-option
        v-for="item in languageArray.properties"
        :key="item.index"
        :label="item.longName"
        :value="item.extendedName"
      >
      </el-option>
    </el-select>

    <slot></slot>
  </div>
</template>

<script>
import Vue from "vue";
import ElementUI from "element-ui";
import LocalizationService from "../../api-services/localization.service";
import { languages } from "../../plugins/i18n";

Vue.use(ElementUI);

export default {
  name: "language-picker",
  props: ["title", "id"],
  data() {
    return {
      languageArray: languages,
    };
  },
  computed: {
    lang: {
      get: function () {
        return this.$store.state.locale;
      },
      set: function (newLang) {
        this.$store.dispatch("changeLocale", newLang);
      },
    },
  },
  destroyed() {
    if (this.$el && this.$el.parentNode) {
      this.$el.parentNode.removeChild(this.$el);
    }
  },
  watch: {
    lang: async function (newLang) {
      // Change language in the backend
      let language = this.$store.state.locales.properties[newLang];

      let _this = this;
      await LocalizationService.changeLanguage(language)
        .then((result) => {
          if (result.status == 200) {
            this.$message.success(
              this.$t("Language set to:") + result.data.extendedName
            );
          }
        })
        .catch((err) => {
          for (var key in err.response.data) {
            err.response.data[key].forEach((x) => {
              _this.$notify({
                type: key,
                message: x,
              });
            });
          }
        });
    },
  },
};
</script>
<style></style>



