// Import language files
import en from "@/locales/en-US/resources";
import hu from "@/locales/hu-HU/resources";

export function loadLocaleMessages() {
  // Set locales object where we store the languages
  const locales = {
    properties: {
      "en-US": {
        shortName: "en",
        longName: "English",
        extendedName: "en-US",
        index: 0
      },
      "hu-HU": {
        shortName: "hu",
        longName: "Magyar",
        extendedName: "hu-HU",
        index: 1
      }
    },
    "en-US": en,
    "hu-HU": hu
  };

  return locales;
}



