<template>
  <b-modal
    v-if="type == 'confirm'"
    ref="confirm-modal"
    v-model="visible"
    centered
    :title="title"
    header-class="bg-my-light"
    footer-class="bg-my-light"
  >
    <template #modal-title>
      <h3>
        <b-icon-question-circle-fill
          class="text-dark mr-3"
        ></b-icon-question-circle-fill
        >{{ title }}
      </h3>
    </template>

    <b-container fluid>
      <h5 class="text-muted">{{ message }}</h5>
    </b-container>

    <div slot="modal-footer">
      <b-button :variant="variant" @click="confirm(true)">
        {{ $t("Yes") }}
      </b-button>
      <b-button
        variant="outline-primary"
        class="outline ml-2"
        @click="confirm(false)"
      >
        {{ $t("No") }}
      </b-button>
    </div>
  </b-modal>

  <b-modal
    v-else-if="type == 'alert'"
    ref="alert-modal"
    v-model="visible"
    centered
    :title="title"
    header-class="bg-my-light"
    footer-class="bg-my-light"
  >
    <template #modal-title>
      <h3>
        <b-icon-exclamation-circle-fill
          :class="`text-${variant} mr-3`"
        ></b-icon-exclamation-circle-fill
        >{{ title }}
      </h3>
    </template>

    <b-container fluid>
      <h5 class="text-muted">{{ message }}</h5>
    </b-container>

    <div slot="modal-footer">
      <b-button :variant="variant" class="float-right" @click="hide">
        {{ $t("Ok") }}
      </b-button>
    </div>
  </b-modal>
</template>

<script>
import { EventBus } from "../../event-bus.js";

export default {
  data() {
    return {
      visible: null,
      title: null,
      message: null,
      variant: null,
      type: null,
      callback: {},
    };
  },
  methods: {
    confirm(value) {
      if (typeof this.callback === "function") {
        this.callback(value);
      }

      this.hide();
    },
    hide() {
      this.visible = false;
    },
    show(data) {
      this.title = data.title;
      this.message = data.message;
      this.variant = data.variant;
      this.type = data.type;
      if (data.callback) {
        this.callback = data.callback;
      }
      this.visible = true;
    },
  },
  beforeMount() {
    EventBus.$on("show", (data) => {
      this.show(data);
    });
  },
};
</script>

<style>
.bg-my-light {
  background-color: #ebebeb;
}
</style>



